section {
    display: flex;
    height: auto;
    margin-left: 20%;
    margin-right: 20%;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;
}

section > .info > input {
    color: rgb(61, 145, 240);
    height: 1.3rem;
    font-size: 1rem;
    width: 100%;
}

.placeholder {
    display: flex;
    flex-direction: column;
    width: 300px;
    padding: 37px;
    background: linear-gradient(49deg, #09031F 50%, #0F1854 100% );
    align-items: center;
}

.placeholder > svg {
    width: 100%;
    height: auto;
    border-style: solid;
    border-color: #77d1eec7;
    border-width: 0.1rem;
}

#logo {
    width: 40%;
    align-self: flex-end;
    padding-top: 1.5rem;
}


.info {
    display: flex;
    width: 373px;
    margin-top: 1.2rem;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
}

section > .info > button {
    width: 29%;
    height: auto;
    color: gainsboro;
    background: #E57373;
    margin: 9px;
    padding: 0.5rem;
    align-self: center;
}

.popup {
    display: flex;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background:linear-gradient(49deg, #2196f342 50%, #00bcd452 100% );
  }
  
  .pic {
    margin: auto;
    display: block;
    width: 80%;
    max-width: 700px;
  }
  
  .close {
    color: red;
    font-size: 40px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #bbb;
    text-decoration: none;
    cursor: pointer;
  }
  